/* Position */
.absolute {position: absolute;}
.relative {position: relative;}

/* Display */
.flex {display: flex;}

/* Flexbox */
.flex-col {flex-direction: column;}

.flex-wrap {flex-wrap: wrap;}

.justify-between {justify-content: space-between;}
.justify-center  {justify-content: center;}

.items-center {align-items: center;}

/* Overflow */
.overflow-hidden {overflow: hidden;}


/* Margin */
.m5  {margin: 5px;}

.mr5  {margin-right:  5px;}
.mr10 {margin-right: 10px;}

.mb5 {margin-bottom: 5px;}

.mx-auto {
  margin-right: auto;
  margin-left:  auto;
}

/* Border */
.border-black {border: 1px solid #000;}
.border-gray  {border: 1px solid #999;}

/* Padding */
.p5 {padding: 5px;}

/* Width */
.width-1\/5 {width: 20%;}
.width-1\/3 {width: 33.33%}
.width-1\/2 {width: 50%}
.width-4\/5 {width: 80%;}
.width-full {width: 100%;}

.width-100 {max-width: 100px;}
.width-600 {max-width: 600px;}

/* Background Color */
.bg-white {background-color: #fff;}

.bold {font-weight: bold;}

.text-center {text-align: center;}

.text-gray {color: #999;}

.text-l  {font-size: 14pt;}
.text-xl {font-size: 24pt;}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pointer {cursor: pointer;}

.whitespace-pre {
  white-space: pre;
}

#addItemButton {
  min-height: 65px;
}

#cycleItemTypeButton,
#toggleItemFirstButton {
  width : 30px;
  height: 30px;
}

@media only screen and (min-width: 601px) {
  .desktop-flex-row-1 {flex-grow: 1;}
  .desktop-flex-row {flex-direction: row;}

  .desktop-mb0 {margin-bottom: 0;}

  .desktop-width-100 {width: 100px;}

  .desktop-text-l {font-size: 14pt;}
}

